.backdrop-mobile.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: translateY(-100%);
}

.backdrop-mobile {
  max-width: 250px;
  width: 100%;
  height: 100vh;
  background: var(--bgc-modal);
  visibility: visible;
  opacity: 1;
  z-index: 2;
  transition: transform 1.2s cubic-bezier(.4, 0, .2, 1), visibility 1.2s cubic-bezier(.4, 0, .2, 1), opacity .7s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
}

.mobile-menu__button {
  width: 20px;
  height: 20px;
